import axios from 'axios';
import React, { useContext, useEffect, useState } from 'react'
// const API_URL=`http://localhost:7000/api/movie/random`;
export const API_URL=`https://netflixclone-k728.onrender.com/api/movie/random`;
const AppContext=React.createContext();
const categorurl='https://netflixclone-k728.onrender.com/api/movie/cat'
// we need to create a provider
const AppProvider=({children})=>{
    const [Categories,setCategories]=useState([]);
    
    const [limit, setlimit] = useState(4)
    const [Actionmovie,setActionMovies]=useState(null);
    const [Allmovies,setAllmovies]=useState([]);
    const [Comedynmovie,setComedyMovies]=useState([]);
    const [Musicmovie,setMusicMovies]=useState([]);
    const [Sportsmovie,setSportsMovies]=useState([]);
    const [Dramamovie,setDramaMovies]=useState([]);
    const [Fantasymovie,setFantasyMovies]=useState([]);
    const [Action, setAction] = useState(["Action"])
    const [type, setType] = useState(["movie"])
    const [Comedy, setComedy] = useState(["Comedy"])
    const [Music, setMusic] = useState(["Music"])
    const [Sports, setSports] = useState(["Sports"])
    const [Fantasy, setFantasy] = useState(["Fantasy"])
    const [Drama, setDrama] = useState(["Drama"])
    const [isLoading, setisLoading] = useState(true)
    const [Error, setError] = useState()
    console.log(Categories)
    useEffect(() => {
      getCategories()
         getActionMovies(API_URL)
        getFantasyMovies(API_URL)
        getComedMovies(API_URL)
        getDramaMovies(API_URL)
        getSportsMovies(API_URL)
        getMusicMovies(API_URL)
        getMovies(API_URL)
      }, [API_URL]);
      // Getmovies
      const getCategories=async()=>{
        setisLoading(true)
        await  axios
            .get(categorurl)
            .then((response) => {
              setisLoading(false)
                 setCategories(response.data)
               
            })
             .catch((error) =>{
              console.log(error)
              setError(true)
              setisLoading(false)
            })
      }
      const getMovies=async(url)=>{
        setisLoading(true)
        await  axios
            .post(url,{limit})
            .then((response) => {
              setisLoading(false)
                 setAllmovies(response.data.result)
               
               console.log(response.data.result)
            })
             .catch((error) =>{
              console.log(error)
              setError(true)
              setisLoading(false)
            })
      }
      const getActionMovies=(url)=>{
        setisLoading(true)
          axios
            .post(url, { genre: Action,limit })
            .then((response) => {
              setisLoading(false)
              setActionMovies(response.data.result)
              console.log({ActionMovies:response.data.result})
            })
            .catch((error) =>{
              console.log(error)
              setError(true)
              setisLoading(false)
            })
      }
      const getComedMovies=(url)=>{
        setisLoading(true)
          axios
            .post(url, { genre: Comedy,limit })
            .then((response) => {
              setisLoading(false)
              setComedyMovies(response.data.result)})
             .catch((error) =>{
              console.log(error)
              setError(true)
              setisLoading(false)
            })
      }
      const getMusicMovies=(url)=>{
        setisLoading(true)
          axios
            .post(url, { genre: Music,limit })
            .then((response) => {
              setisLoading(false)
              setMusicMovies(response.data.result)})
             .catch((error) =>{
              console.log(error)
              setError(true)
              setisLoading(false)
            })
      }
      const getSportsMovies=(url)=>{
        setisLoading(true)
          axios
            .post(url, { genre: Sports,limit })
            .then((response) =>{
              setisLoading(false)
              setSportsMovies(response.data.result)
            })
             .catch((error) =>{
              console.log(error)
              setError(true)
              setisLoading(false)
            })
      }
      const getDramaMovies=(url)=>{
        setisLoading(true)
          axios
            .post(url, { genre: Drama ,limit })
            .then((response) => {
              setisLoading(false)
              setDramaMovies(response.data.result)
            })
             .catch((error) =>{
              console.log(error)
              setError(true)
              setisLoading(false)
            })
      }
      const getFantasyMovies=(url)=>{
          setisLoading(true)
          axios
            .post(url, { genre: Fantasy ,limit })
            .then((response) => {
              setisLoading(false)
              setFantasyMovies(response.data.result)
            })
             .catch((error) =>{
              console.log(error)
              setError(true)
              setisLoading(false)
            })
      }
const state={
  Error,Allmovies,Categories,isLoading, Actionmovie,Fantasymovie,Comedynmovie,Dramamovie,Sportsmovie,Musicmovie
}
    return   <AppContext.Provider value={state}>{children}</AppContext.Provider>;
};
const useGlobalContext=()=>{
    return useContext(AppContext);
}
export {AppContext,AppProvider,useGlobalContext}