import React, { useEffect, useState } from "react";
import axios from "axios";
import { Link, useParams } from "react-router-dom";
import instance from "../config/axios";
import ProductCard from "./ProductCard";
import { useGlobalContext } from "../config/context";
import Pagination from "./Pagination";
import { ErrorComponent } from "./eroorsandloderscomponent/Error";
import { Loader } from "./eroorsandloderscomponent/Loader";

const GenreCategory = () => {
  const { Categories } = useGlobalContext();
  const [movies, setMovies] = useState([]);
  const [pageNumber, setPageNumber] = useState(1);
  const [numberOfPages, setNumberOfPages] = useState(0);
  const [limit, setLimit] = useState(8);
  const [isLoading, setisLoading] = useState(true)
  const [Error, setError] = useState()
  const { genre } = useParams();

  useEffect(() => {
    const fetchData = () => {
      setisLoading(true)
      axios
        .post(instance.baseURL + "movie/random", {
          genre: [genre],
          limit,
          page: pageNumber,
        })
        .then((response) => {
          setisLoading(false)
          setMovies(response.data.result);
          setNumberOfPages(response.data.totalPage);
        })
        .catch((error) =>{
          console.log(error)
          setError(true)
          setisLoading(false)
        })
    };
    fetchData();
  }, [pageNumber, genre]);

  const gotoPrevious = () => {
    setPageNumber(Math.max(1, pageNumber - 1));
  };
  const gotoNext = () => {
    setPageNumber(Math.min(numberOfPages, pageNumber + 1));
  };

  const getVisiblePages = () => {
    const visiblePages = [];
    const maxVisiblePages = 5;
    const halfMaxVisiblePages = Math.floor(maxVisiblePages / 2);

    let startPageIndex = Math.max(1, pageNumber - halfMaxVisiblePages);
    let endPageIndex = Math.min(numberOfPages, pageNumber + halfMaxVisiblePages);

    if (endPageIndex - startPageIndex < maxVisiblePages - 1) {
      if (startPageIndex === 1) {
        endPageIndex = Math.min(numberOfPages, startPageIndex + maxVisiblePages - 1);
      } else {
        startPageIndex = Math.max(1, endPageIndex - maxVisiblePages + 1);
      }
    }

    for (let i = startPageIndex; i <= endPageIndex; i++) {
      visiblePages.push(i);
    }

    return visiblePages;
  };

  return (
    <>
    
      <section class="gen-section-padding-3">
        <div class="container">
        <div className="row">
       
            {isLoading ? (
            <div className="col-12">
              <center><Loader /></center>
            </div>
          ) : Error ? (
            <div className="col-12">
              <ErrorComponent />
            </div>
          ) : (
            movies.map((movie) => {
              return (
               
                  <ProductCard movie={movie} />
              
                ) ;
            })
            
          )}
            </div>
              
           
            <div>
              <button className="prebtn" onClick={gotoPrevious}>
                Previous
              </button>
              {getVisiblePages().map((pageIndex) => (
                <button
                  className={`allpages ${pageNumber === pageIndex ? "active" : ""}`}
                  key={pageIndex}
                  onClick={() => setPageNumber(pageIndex)}
                >
                  {pageIndex}
                </button>
              ))}
              <button className="nextbtn" onClick={gotoNext}>
                Next
              </button>
            </div>
         
        </div>
      </section>
    </>
  );
};

export default GenreCategory;
