import React from 'react'
import { Link } from 'react-router-dom';

const ProductCard = ({movie}) => {
  return (
    <div class="col-xl-3 col-lg-4 col-md-6">
    <div class="gen-carousel-movies-style-3 movie-grid style-3">
      <div class="gen-movie-contain">
        <div class="gen-movie-img">
          <img  width={"100%"} src={movie.imgSm} alt={movie.imgSm} />
          <div class="gen-movie-add">
            <div class="wpulike wpulike-heart">
              <div class="wp_ulike_general_class">
                <a href="#" class="sl-button text-white">
                  <i class="far fa-heart"></i>
                </a>
              </div>
            </div>
            <ul class="menu bottomRight">
              <li class="share top">
                <i class="fa fa-share-alt"></i>
                <ul class="submenu">
                  <li>
                    <a href="#" class="facebook">
                      <i class="fab fa-facebook-f"></i>
                    </a>
                  </li>
                  <li>
                    <a href="#" class="facebook">
                      <i class="fab fa-instagram"></i>
                    </a>
                  </li>
                  <li>
                    <a href="#" class="facebook">
                      <i class="fab fa-twitter"></i>
                    </a>
                  </li>
                </ul>
              </li>
            </ul>
            <div class="video-actions--link_add-to-playlist dropdown">
              <a
                class="dropdown-toggle"
                href="#"
                data-toggle="dropdown"
              >
                <i class="fa fa-plus"></i>
              </a>
              <div class="dropdown-menu mCustomScrollbar">
                <a class="login-link" href="#">
                  Sign in to add this video to a playlist.
                </a>
              </div>
            </div>
          </div>
          <div class="gen-movie-action">
          <Link to={"/singlemovie/"+movie._id}  class="gen-button">
<i class="fa fa-play"></i>
</Link>
          </div>
        </div>
        <div class="gen-info-contain">
          <div class="gen-movie-info">
            <h3>
              <a href="index.html">{movie.title}</a>
            </h3>
          </div>
          <div class="gen-movie-meta-holder">
            <ul>
              <li>2 weeks</li>
              <li>
                {
                  // console.log({genre:movie.genre})
                  movie.genre.map((genre, idx) => {
                    return (
                      <span key={idx}>
                        <Link
                          className="item-link"
                          to={"/genre/" + genre}
                        >
                          {(idx ? "," : "") + genre}
                        </Link>
                      </span>
                    );
                  })
                }
              </li>
            </ul>
          </div>
        </div>
      </div>
    </div>
  </div>
  )
}

export default ProductCard