import React, { useState, useEffect, useRef } from "react";
import { Skeleton, Box } from "@mui/material";
import axios from "axios";
import { Link } from "react-router-dom";
import { PulseLoader } from "react-spinners";
import CarouselCard from "./CarouselCard";
import { Loader } from '../eroorsandloderscomponent/Loader'
import { ErrorComponent } from '../eroorsandloderscomponent/Error'
const Caroesel = ({ Loading,Error, movies, Headingtitle }) => {
  return (
    <>
      <h3> {Headingtitle}</h3>
      {Loading ? (
        <div className="text-center">
          <Loader />
        </div>
      ) : Error ? (
        <div className="text-center">
          <ErrorComponent />
        </div>
      ) :  movies ? (
        <div className="row mt-3">
          {/* All Movies Rendering */}
          {movies.length > 0 &&
            movies.map((h) => {
              return (
                 <CarouselCard
                 imgSm={h.imgSm}
                 id={h._id}
                 genre={h.genre}
                 title={h.title}
                 />
              );
            })}
          {/* End All Movies rendering */}
        </div>
      ) : (
        // Show loading indicator or error message
        <h1>{Loading ? "Loading" : "Movies not available"}</h1>
      )
      }
    </>
  );
};

export default Caroesel;
